import tuiButton from "@/components/extend/button/button"
import tuiDatetime from "@/components/dateTime/dateTime"
export default {
	components: {
		tuiButton,
		tuiDatetime
	},
	data() {
		return {
			type: 1,
			startYear: 1980,
			endYear: 2030,
			cancelColor: "#888",
			color: "#5677fc",
			setDateTime: "",
			result: ""
		}
	},
	methods: {
		show: function(type) {
			this.cancelColor = "#888";
			this.color = "#5677fc";
			this.setDateTime = "";
			this.startYear = 1980;
			this.endYear = 2030;
			switch (type) {
				case 1:
					//this.setDateTime = "2019-10-12";
					this.type = 2;
					break;
				case 2:
					//this.setDateTime = "2019-11";
					this.type = 3;
					break;
				case 3:
					// this.setDateTime = "18:01";
					this.type = 4;
					break;
				case 4:
					this.type = 1;
					break;
				case 5:
					this.type = 1;
					this.setDateTime = "2019-09-12 18:01";
					break;
				case 6:
					this.type = 1;
					this.startYear = 2019;
					this.endYear = 2021;
					break;
				case 7:
					this.type = 1;
					this.cancelColor = "#555";
					this.color = "#e41f19";
					break;
				default:
					break;
			}
			this.$refs.dateTime.show()
		},
		change: function(e) {
			console.log(e)
			this.result = e.result
		}
	}
}
